import { cilChatBubble, cilMinus, cilPlus, cilX} from '@coreui/icons'
import CIcon from '@coreui/icons-react'
import {  
  CButton,
  CCard,
  CCardBody,
  CCol,
  CForm,
  CFormInput,
  CFormSelect,
  CFormSwitch,
  CFormTextarea,
  CRow
} from '@coreui/react'
import React, { useEffect, useState } from 'react'
import { Link, generatePath,  json,  useNavigate, useParams } from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify'
import  './cms.scss';

import { BiBookContent, BiImageAlt, BiListCheck, BiParagraph } from "react-icons/bi";
import { PiTextTBold } from "react-icons/pi";
import { BsLink45Deg } from 'react-icons/bs'
import { IoMdCopy} from 'react-icons/io';
import { AiOutlineMinus } from "react-icons/ai";





const AddPages = () => { 
  const Navigate = useNavigate()
  const [validated, setValidated] = useState(false)
  const [loader, setLoader] = useState(false);
  const [activeSect, setActiveSect] = useState(0);
  const [selectedSect, setSelectedSect]  = useState("");  
  const [values, setValues]  = useState([]);

  let [mainArr, setMainArr] = useState([]);
  let [pageInfo, setPageInfo] = useState({});
  const {id}=useParams();

  

  const handleSubmit = (e) => {
    e.preventDefault();
    const form = e.currentTarget;
    const [name, description, meta_title, meta_description, status] = e.target

    if (form.checkValidity() === false) {
      setValidated(true)
      e.stopPropagation()
    } else {
      setValidated(false);
      var requestOptions = {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          _id:id,
          page_name: name.value,
          page_description: description.value || '',
          meta_title: meta_title.value || '',
          meta_description: meta_description.value || '',
          status: status.value || 1,
        }),
      }

      const url = process.env.REACT_APP_API_URL + 'pages/update'
      setLoader(true);
      fetch(url, requestOptions)
        .then((response) => response.json())
        .then((result) => {
          if (result.status == 200) {
            toast.success(result.message)           
            setValidated(false);                      
            addContent(result.data);
          } else {
            toast.error(result.message)
          }
        })
        .catch((error) => console.log('error', error))
    }
  }


  const addContent = (data) => {
    setLoader(true); 
  
    if (mainArr.length) {
      const requests = mainArr.map((item) => {
        const newItem = { ...item };
        newItem.section_value = JSON.stringify(item.section_value);
        newItem.page_id = id;
        let requestOptions = {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(newItem),
          redirect: 'follow',
        };  

        if(newItem._id){ 
        return fetch(process.env.REACT_APP_API_URL + 'cms/update-one', requestOptions)
          .then((response) => response.json())
          .then((result) => {
            return result; // Return the result for each request
          })
          .catch((error) => {
            console.log('error', error);
            throw error;
          });
        }else{
          return fetch(process.env.REACT_APP_API_URL + 'cms/create', requestOptions)
          .then((response) => response.json())
          .then((result) => {
            return result; // Return the result for each request
          })
          .catch((error) => {
            console.log('error', error);
            throw error;
          });
        }

      });

      Promise.all(requests)
        .then((results) => {
          setLoader(false);
          toast.success('Finally Updated all additional content.');
        })
        .catch((error) => {          
          setLoader(false);
          toast.error('Error updating additional content.');
          console.error('Error updating content:', error);
        });
    }
  };


  const handleRemove = async (key, item)=>{
    if(!confirm("Are you Sure to delete this Section?")){
      return ;
    }
    if(item._id){
      console.log(item._id);
      try{       
        let requestOptions = {
          method: 'POST',
          headers: {"Content-Type":"application/json"},
          body: JSON.stringify({
            "_id": item._id
          }),
          redirect: 'follow'
        };

      const deleteFetch = await fetch(process.env.REACT_APP_API_URL+"cms/delete", requestOptions);

      const deleted = await deleteFetch.json();
      console.log(deleted);
      toast.success("Successfully deleted data.");

      }catch(e){
        toast.error("Error while deleting data.")
      }
    }    
    setMainArr(prev=>prev.splice(key, 1));
  }
  
  






  const handleSection = (e)=>{
    setSelectedSect(e);
    setActiveSect(2)
  } 

  const handleAdd = ()=> setActiveSect(1);

  const fieldTypes = [
    {
      name:"Short Text",
      icon:<PiTextTBold/>,
      type:"text"
    },
    {
      name:"Long Text",
      icon:<BiParagraph/> ,
      type:"textarea"
    },
    {
      name:"Image",
      icon:<BiImageAlt/>,
      type:"img"
    },    
    {
      name:"Link",
      icon:<BsLink45Deg/>,
      type:"url"
    }
  ]
const generateKey = (arg)=>arg+"_"+Math.floor(Math.random()*99999);
const handleField =(type, id)=>{  
  setMainArr((prev)=>{  
    setActiveSect(0)
    return [...prev, {section_prop:generateKey("section"),section_name:"Section "+prev.length, section_value:[{type, id}]}]
  })
}


const handleAddMore = (key, subKey)=>{
  const myArr = [...mainArr];
  myArr[key]["section_value"][subKey]["moreField"]=true;  
  setMainArr(myArr);
}

const addMoreField = (type, id, key,subKey)=>{
  const myArr = [...mainArr];
  myArr[key]["section_value"].push({type, id});
  myArr[key]["section_value"][subKey]["moreField"]=false;
  setMainArr(myArr);  
}
const handleRepeater = (type, id)=>{
  setMainArr((prev)=>{  
    setActiveSect(0)
    return [...prev, {section_prop:generateKey("section"),section_name:"Section "+prev.length, section_value:[[{type, id}]]}]
  });
}

const repeatAddMore = (key, subKey, k)=>{
  const myArr = [...mainArr];
  myArr[key]["section_value"][subKey][k]["moreField"]=true;  
  setMainArr(myArr);
}
const repeatMoreField =(type, id, key,subKey, k)=>{
  const myArr = [...mainArr];
  myArr[key]["section_value"][subKey].push({type, id});
  myArr[key]["section_value"][subKey][k]["moreField"]=false;
  setMainArr(myArr);  
}

const [developer, setDeveloper] = useState(false);
const handleDeveloperOption = (arg)=>{
  if(arg.target.checked){
    let arConfirm = confirm("Are you sure you want to activate Developer option? If you are not able to handele it can break your layout. ")
    if(!arConfirm){
      arg.target.checked=false;  
      setDeveloper(false)   
    }else{
      setDeveloper(true)
    }    
  }
}

const handleRepeat =(key, subKey)=>{
  const myArr = [...mainArr];
  let texts = myArr[key]["section_value"][subKey];
  myArr[key]["section_value"].push(texts);
  setMainArr(myArr); 
}


//This event is for change of each field
const handleChange = async(e, key, subKey, type)=>{
  const myArr = [...mainArr];
  if(type==="text"){
    myArr[key]["section_value"][subKey]["value"]=e.target.value;
  }else if(type==="file"){
    setLoader(true);
    console.log(e.target.value);
    try { 
      const formdata = new FormData();
      formdata.append("image", e.target.files[0], e.target.files[0].name);
      formdata.append("type", "image");

      const requestOptions = {
        method: 'POST',
        body: formdata,
        redirect: 'follow',
      };
  
      const response = await fetch(process.env.REACT_APP_API_URL + "images/add", requestOptions);
      const result = await response.json();  

      myArr[key]["section_value"][subKey]["value"]=result.data.image;
      myArr[key]["section_value"][subKey]["file_type"]=result.data.type;
     
      setLoader(false);

    } catch (error) {
      setLoader(false);
      console.error('error', error);
    }


  }else if(type==="url_text"){
    myArr[key]["section_value"][subKey]["url_text"]=e.target.value;
  }else if(type==="url"){
    myArr[key]["section_value"][subKey]["value"]=e.target.value;
  }else{
    alert("Please Enter a attr.")
  }
  console.log(myArr)
  setMainArr(myArr); 

}

//This event is for Repeater of each field
const repeatChange = async(e, key, subKey, m, type)=>{
  const myArr = [...mainArr];
  if(type==="text"){
    myArr[key]["section_value"][subKey][m]["value"]=e.target.value;
  }else if(type==="file"){
    setLoader(true);
    console.log(e.target.value);
    try { 
      const formdata = new FormData();
      formdata.append("image", e.target.files[0], e.target.files[0].name);
      formdata.append("type", "image");

      const requestOptions = {
        method: 'POST',
        body: formdata,
        redirect: 'follow',
      };
  
      const response = await fetch(process.env.REACT_APP_API_URL + "images/add", requestOptions);
      const result = await response.json();  

      myArr[key]["section_value"][subKey][m]["value"]=result.data.image;
      myArr[key]["section_value"][subKey][m]["file_type"]=result.data.type;
     
      setLoader(false);

    } catch (error) {
      setLoader(false);
      console.error('error', error);
    }


  }else if(type==="url_text"){
    myArr[key]["section_value"][subKey][m]["url_text"]=e.target.value;
  }else if(type==="url"){
    myArr[key]["section_value"][subKey][m]["value"]=e.target.value;
  }else{
    alert("Please Enter a attr.")
  }
  setMainArr(myArr); 
}




useEffect(()=>{
var requestOptions = {
  method: 'POST',
  headers: {"Content-Type": "application/json"},
  body: JSON.stringify({
    "page_id": id
  }),
  redirect: 'follow'
};

fetch(process.env.REACT_APP_API_URL+"cms/list", requestOptions)
  .then(response => response.json())
  .then(result => {
    if(result.data){
    let temp = result.data.map(i=>{
      try {
        i.section_value = JSON.parse(i.section_value);
      } catch (error) {
        console.error('Error parsing section_value:', error);
        // Handle the parsing error here, e.g., set it to an empty array
        i.section_value = [];
      }
      return i;
    })
    setMainArr(temp)
    }
  })
  .catch(error => console.log('error', error));



fetch(process.env.REACT_APP_API_URL+"pages/list", {
  method: 'POST',
  headers: {"Content-Type": "application/json"},
  body:JSON.stringify({_id:id}),
  redirect: 'follow'
})
  .then(response => response.json())
  .then(result => {
    setPageInfo(result.data[0])
    console.log(result)
  })
  .catch(error => console.log('error', error));

 

},[]);

  return (
    <>
      <ToastContainer />
      {loader&&
      <div className="loader-container">
        <div className="loader"></div>
      </div>}
      
      <h1 className="headingType1 text-center mb-4">Pages</h1>
      <CForm
        className="g-3 needs-validation"
        noValidate
        validated={validated}
        onSubmit={handleSubmit}
      >
        <CRow className="g-5 justify-content-center">
          <CCol lg={8}>
            <CCard>
              <CCardBody className="p-5">
                <div className="mb-4">
                  <CFormInput
                    type="text"
                    defaultValue={pageInfo.page_name}
                    feedbackInvalid="Please enter a Brand Name."
                    id="validationCustom1"
                    label="Page Name"
                    name="name"
                    required
                  />
                  <p>Your Generated Url Slag: {"/"+pageInfo.page_slug}</p>
                </div>
                <div className="mb-4">
                  <CFormTextarea
                    defaultValue={pageInfo.page_description}
                    feedbackInvalid="Please Select Status."
                    label="Description"
                    id="validationCustom2"
                    name="description"
                    rows={6}
                  />
                </div>
              </CCardBody>
            </CCard>
          </CCol>
          <CCol lg={4}>
            <CCard>
              <CCardBody className="p-5">
                <div className="mb-4">
                  <CFormInput
                    type="text"
                    defaultValue={pageInfo.meta_title}
                    feedbackInvalid="Please enter a Brand Name."
                    id="validationCustom3"
                    label="Meta Title"
                    name="meta_title"
                  />
                </div>
                <div className="mb-4">
                  <CFormTextarea
                    defaultValue={pageInfo.meta_description}
                    feedbackInvalid="Please Select Status."
                    label="Meta Description"
                    id="validationCustom3"
                    name="meta_description"
                  />
                </div>
                <div className="mb-4">
                  <CFormSelect
                    type="text"
                    defaultValue=""
                    feedbackInvalid="Please Select Status."
                    id="validationCustom4"
                    label="Status"
                    name="status"
                    required
                  >
                    <option value={'1'}>Active</option>
                    <option value={'0'}>Inactive</option>
                  </CFormSelect>
                </div>
                <div className="text-center mt-4">
                  <CButton color="primary" type="submit" className="py-2 px-5">
                    Update Page
                  </CButton>
                </div>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </CForm>

      <CCard  className={`mt-4 ${developer?"i_am_a_dev":""}`}>
        <CCardBody className="p-5">
          <CRow>
            <CCol lg>
              <h4 className='mb-4'>Sections</h4>
            </CCol>
            <CCol lg="auto">
              <CFormSwitch onChange={handleDeveloperOption} label="Developer Option" id="formSwitchCheckDefault"/>
            </CCol>
          
          </CRow>

          {mainArr.map((item, key)=>(
            <div className='each_sections' key={key}>
              {console.log(item)}
              <div className='each_section_header'>
                <CFormInput defaultValue={item.section_name} placeholder='Section Title...'/>
                <div className='copySectionProp' data-key={key} title={item._id} onClick={()=>handleRemove(key, item)}><AiOutlineMinus /></div>
              </div>
              <div className='each_section_body'>
              {item.section_value && item.section_value.map((subItem, subKey)=>(<div key={subKey}>                
                 {!Array.isArray(subItem)&&<>
                <div className='each_single_items mb-3'> 
                  {subItem.type==="text"&&<CFormInput label="Short Text.." className="" defaultValue={subItem.value} onChange={(e)=>handleChange(e, key, subKey,"text")}/>}
                  {(subItem.type==="img" && subItem.value)&&<img src={process.env.REACT_APP_BASE_URL+subItem.value} width={"100px"} style={{"display":"block"}}/>}
                  {subItem.type==="img"&&<CFormInput label="Upload Image.." type='file' className=""  onChange={(e)=>handleChange(e, key, subKey, "file")}/>}
                  {subItem.type==="textarea"&&<CFormTextarea  label="Enter Long Text.." className="" defaultValue={subItem.value} onChange={(e)=>handleChange(e, key, subKey, "text")}/>} 
                  {subItem.type==="url"&&<CRow>
                    <CCol>
                      <CFormInput label="Link Text" className="" defaultValue={subItem.value} onChange={(e)=>handleChange(e, key, subKey, "url_text")}/>
                    </CCol>
                    <CCol>
                      <CFormInput label="Link Value" className="" defaultValue={subItem.value} onChange={(e)=>handleChange(e, key, subKey, "url")}/>
                    </CCol>
                  </CRow>} 

                  {!subItem.moreField &&                  
                    <button className='arPlusIcon'  onClick={()=>handleAddMore(key, subKey)}><CIcon icon={cilPlus}/></button>
                  }                  
                  {subKey>0 &&      
                    <button className='arRemoveIcon'  onClick={()=>handleAddMore(key, subKey)}><CIcon icon={cilMinus}/></button>
                  }  
                  
                  </div>  
                  {subItem.moreField && 
                    <CRow className='mt-4 mb-4'>
                      {fieldTypes.map((i, k)=>(
                        <CCol lg={3} key={k}>
                          <div  className='each_section_Wrp' onClick={(e)=>addMoreField(i.type, generateKey(i.type), key, subKey)} data-id={generateKey(i.type)}>
                            <div className='aicon'>
                              {i.icon}           
                            </div>
                            <div className='section_title'>
                              {i.name}
                            </div>
                          </div>
                        </CCol>
                      ))} 
                    </CRow>}

                  
                   
                  </>} 
                 
                  {Array.isArray(subItem)&&<div className='each_repeater_wrapper'>                 
                    {subItem.map((a, m)=>(
                      <div className='each_repeater_item mb-3' key={m}>
                      {a.type==="text"&&<CFormInput label="Short Text.." className="" defaultValue={subItem.value} onChange={(e)=>repeatChange(e,key, subKey, m, "text")}/>}
                      {a.type==="img"&&<CFormInput label="Upload Image.." type='file' className="" defaultValue={subItem.value} onChange={(e)=>repeatChange(e,key, subKey, m, "file")}/>}
                      {a.type==="textarea"&&<CFormTextarea  label="Enter Long Text.." className="" defaultValue={subItem.value} onChange={(e)=>repeatChange(e,key, subKey, m, "text")}/>} 
                      {a.type==="url"&&<CRow>
                        <CCol>
                          <CFormInput label="Link Text" className="" defaultValue={subItem.value} onChange={(e)=>repeatChange(e,key, subKey, m, "url_text")}/>
                        </CCol>
                        <CCol>
                          <CFormInput label="Link Value" className="" defaultValue={subItem.value} onChange={(e)=>repeatChange(e,key, subKey, m, "url")}/>
                        </CCol>
                      </CRow>}  

                      {a.moreField && 
                        <CRow className='mt-4 mb-4'>
                          {fieldTypes.map((i, k)=>(
                            <CCol lg={3} key={k}>
                              <div  className='each_section_Wrp' onClick={()=>repeatMoreField(i.type, generateKey(i.type), key, subKey, m)}>
                                <div className='aicon'>
                                  {i.icon}           
                                </div>
                                <div className='section_title'>
                                  {i.name}
                                </div>
                              </div>
                            </CCol>
                          ))} 
                        </CRow>}

                      {!a.moreField && <button className='arPlusIcon'  onClick={()=>repeatAddMore(key, subKey, m)}><CIcon icon={cilPlus}/></button>} 
                                  
                      {m>0 &&      
                        <button className='arRemoveIcon'  onClick={()=>repeatRemoveItem(key, subKey, k)}><CIcon icon={cilMinus}/></button>
                      } 
                      </div>
                    ))}

                    <div className='text-end'>
                      <CButton color='info' onClick={()=>handleRepeat(key, subKey)}><IoMdCopy/> Repeat</CButton>
                    </div>
                  
                  </div>}            
                
                </div>
              ))}                 
              


              </div>
            </div>
          ))}

          {activeSect==2&&selectedSect=="single"&& 
            <div className='single_object_selected mb-5'>
                <CRow>
                  {fieldTypes.map((i, key)=>(
                    <CCol lg={3} key={key}>
                      <div className='each_section_Wrp' onClick={(e)=>handleField(i.type, generateKey(i.type))}>
                        <div className='aicon'>
                           {i.icon}           
                        </div>
                        <div className='section_title'>
                          {i.name}
                        </div>
                      </div>
                    </CCol>
                  ))} 
                </CRow>
            </div>
          }
          {activeSect==2&&selectedSect=="repeater"&& 
            <div className='repeater_object_selected mb-5'>
                <CRow>                 
                  {fieldTypes.map((i, key)=>(
                    <CCol lg={3} key={key} onClick={()=>handleRepeater(i.type, generateKey(i.type))}>                      
                      <div className='each_section_Wrp'>
                        <div className='aicon'>
                           {i.icon}           
                        </div>
                        <div className='section_title'>
                          {i.name}
                        </div>
                      </div>
                    </CCol>
                  ))}
                </CRow>
                {/* <div className='text-center mt-3'>
                  <CButton color='info' onClick={handleSelect}>Select</CButton>
                </div> */}
            </div>
          }
          {activeSect==1 &&
          <div className='allSectionTypeWrp'>
            <div className='removeBtn'>
              <CIcon icon={cilX} customClassName="nav-icon" />
            </div>
            <CRow className='g-3'>              
              <CCol lg="3">
                <div  className='each_section_Wrp' onClick={()=>handleSection("single")}>
                  <div className='aicon'>
                    <BiBookContent/>               
                  </div>
                  <div className='section_title'>
                    Single 
                  </div>
                </div>
              </CCol>
              <CCol lg="3">
                <div className='each_section_Wrp' onClick={()=>handleSection("repeater")}> 
                  <div className='aicon'>
                    <BiListCheck/>               
                  </div>
                  <div className='section_title'>
                    Repeater
                  </div>
                </div>
              </CCol>
            </CRow>
          </div>}
          {activeSect==0&&
          <div className='addNewSection' onClick={handleAdd}>
            <CIcon icon={cilPlus} customClassName="nav-icon" />
          </div>}
        </CCardBody>
      </CCard>
    </>
  )
}

export default AddPages
